html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans", sans-serif;
  background-color: var(--color-mine);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-container {
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1140px;

  @include lg {
    margin-right: 110px;
    margin-left: 150px;
  }

  @include md {
    margin: 0 auto;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
}

.btn {
  padding: 17px 47px;
  max-height: 60px;
  font: var(--middle-font-md);
  letter-spacing: 0.1em;
  text-transform: uppercase;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

  &--gold {
    color: var(--color-white);
    background-color: var(--color-gold);

    &:hover {
      background-color: var(--color-gold-hover);
    }
  }
}

.title {
  margin: 0;
  margin-bottom: 100px;
  font: var(--high-light);
  letter-spacing: 0.1em;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-white);

  div {
    display: inline-block;
    color: var(--color-gold);
  }

  @include md {
    margin-bottom: 60px;
  }

  @include sm {
    font: 300 30px / normal var(--font-family);
  }
}

.wrapper-hero {
  display: grid;
  grid-template: auto 1fr / minmax(150px, 1fr) minmax(850px, 1140px) minmax(150px, 1fr);
  grid-template-areas:
    "side-panel header"
    "side-panel hero";
  margin: 0 auto;
  width: 100%;
  min-height: 1045px;
  background-image: linear-gradient(var(--color-mine-alpha85), var(--color-mine-alpha85)), url("../img/header.jpg");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;

  @include xl {
    max-width: 1920px;
    background-size: cover;
  }

  @include lg {
    grid-column: 1 / 3;
    grid-template-columns: 150px auto;
  }

  @include md {
    grid-template-areas:
      "header"
      "hero";
    grid-template-columns: minmax(100px, 1fr);
    min-height: 800px;
  }

  @include sm {
    min-height: 600px;
    background-image: linear-gradient(var(--color-mine-alpha85), var(--color-mine-alpha85)), url("../img/header-375.jpg");
  }

  @include xs {
    min-height: 400px;
  }
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 101;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;

  &--open {
    opacity: 1;
    visibility: visible;
  }
}
