@mixin xl {
  @media (max-width: (1400px)) {
    @content;
  }
}

@mixin lg {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin md {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin sm {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin xs {
  @media (max-width: (375px)) {
    @content;
  }
}
